import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { SuccessStoriesComponent } from './success-stories/success-stories.component';
import { EventComponent } from './event/event.component';
import { AdmissionProcessComponent } from './admission-process/admission-process.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { SupportUsComponent } from './support-us/support-us.component';
import { NursingComponent } from './services/nursing/nursing.component';
import { OrganicFarmingComponent } from './services/organic-farming/organic-farming.component';
import { RemedialClassesComponent } from './services/remedial-classes/remedial-classes.component';
import { AboutComponent } from './about/about.component';
import { ComputorEducationComponent } from './computor-education/computor-education.component';
import { TailoringComponent } from './services/tailoring/tailoring.component';


const routes: Routes = [
  {path: '\home' ,component:HomeComponent},
  {path: '\success-stories' ,component:SuccessStoriesComponent},
  {path: '\events' ,component:EventComponent},
  {path: '\admission-process' ,component:AdmissionProcessComponent},
  {path: '\contact-us' ,component:ContactUsComponent},
  {path: '\support-us' ,component:SupportUsComponent},
  {path: '\services-nursing' ,component:NursingComponent},
  {path: '\services-organicfarming' ,component:OrganicFarmingComponent},
  {path: '\services-remedialclasses' ,component:RemedialClassesComponent},
  {path:'services-com-education',component:ComputorEducationComponent},
  {path:'services-tailoring',component:TailoringComponent},
  {path: '\about' ,component:AboutComponent}

   
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
